:root {
  --main-font: 'Montserrat', sans-serif;
  --red: #D42124;
  --font-size-regular: 1.8rem;
  --font-size-small: 1.6rem;
  --color-primary: #0079D9;
  --color-primary-light: #0096e1;
  --color-primary-faded: rgba(14, 127, 210, .1);
  --white: #ffffff;
}
.container {
  padding: 0 30px;
  position: relative;
  max-width: 1500px;
  margin: 0 auto;
}

.header {
  background-color: #0079D9;
  background-color: var(--color-primary);
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.navigation-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.navigation-wrapper.open {
  -ms-flex-align: start;
      align-items: flex-start;
}

.navigation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.navigation.open {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.navigation__list {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  font-size: 1.6rem;
  font-size: var(--font-size-small);
}

@media (max-width: 740px) {
  .navigation__list {
    display: none;
  }
}

.navigation__list.open {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-left: 0;
  -ms-flex-align: start;
      align-items: flex-start;
}

.navigation__item {
  display: inline-block;
  text-align: center;
  margin-left: 3rem;
  margin-right: 3rem;
}

@media (max-width: 740px) {
  .navigation__item {
    margin-left: 0;
  }
}

.navigation__link {
  color: #ffffff;
  color: var(--white);
  text-decoration: none;
  display: inline-block;
  padding-bottom: .5rem;
  position: relative;
}

@media (max-width: 740px) {
  .navigation__link {
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 1.8rem;
    font-size: var(--font-size-regular);
  }
}

.navigation__link::after {
  width: 0;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 2px solid #ffffff;
  border-bottom: 2px solid var(--white);
  -webkit-transition: .3s;
  transition: .3s;
}

.navigation__link:hover::after,.navigation__link:focus::after {
  width: 100%;
}

@media (max-width: 740px) {
  .navigation__logo {
    max-width: 200px;
  }
}

.menu-icon {
  display: none;
}

@media (max-width: 740px) {
  .menu-icon {
    display: block;
    width: 25px;
    height: 20px;
    position: relative;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out all;
    transition: .5s ease-in-out all;
    cursor: pointer;
    z-index: 1;
  }

  .menu-icon > span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: #ffffff;
    background: var(--white);
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }

  .menu-icon > span:nth-child(1) {
    top: 0;
  }

  .menu-icon > span:nth-child(2),
  .menu-icon > span:nth-child(3) {
    top: 7px;
  }

  .menu-icon > span:nth-child(4) {
    top: 14px;
  }

  .menu-icon.open {
    top: 13px;
  }

  .menu-icon.open > span:nth-child(1) {
    top: 0;
    width: 0;
    left: 50%;
  }

  .menu-icon.open > span:nth-child(2) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }

  .menu-icon.open > span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }

  .menu-icon.open > span:nth-child(4) {
    top: 18px;
    width: 0;
    left: 50%;
  }
}

.copyright {
  display: inline-block;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.button-submit {
  display: none;
}

.button {
  display: inline-block;
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}

@media (max-width: 500px) {
  .button:first-of-type {
    margin-bottom: 2.5rem;
  }
}

.button__input,
.button__input--csv,
.button__input--xlsx {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.button__label {
  color: #ffffff;
  color: var(--white);
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  padding: 1.5rem 3rem 1.5rem 5rem;
  overflow: hidden;
  background: #0079D9 url(/build/images/upload.2a66dc43.svg) no-repeat left 1.5rem center;
  background: var(--color-primary) url(/build/images/upload.2a66dc43.svg) no-repeat left 1.5rem center;
  display: inline-block;
}

.button__label:hover,.button__label:focus {
  background-color: #0096e1;
  background-color: var(--color-primary-light);
}

.form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media (max-width: 500px) {
  .form {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}

.form > div:last-of-type {
  display: none;
}

.form-error {
  color: #D42124;
  color: var(--red);
  font-size: 1.6rem;
  font-size: var(--font-size-small);
}

.box {
  padding: 10rem 2rem;
  background-color: rgba(14, 127, 210, .1);
  background-color: var(--color-primary-faded);
  width: 100%;
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

@media (max-width: 500px) {
  .box {
    padding: 7rem 2rem;
  }
}

.animation-wrapper:not([class="js-completed"]) {
  display: none;
}

.animation-wrapper.js-completed {
  display: block;
}

.animation-wrapper.js-completed > svg {
  width: 100px;
  display: block;
  margin: 40px auto 0;
}

.path {
  stroke-dasharray: 1000;
  stroke-dashoffset: 0;
}

.path.circle {
  -webkit-animation: dash .9s ease-in-out;
          animation: dash .9s ease-in-out;
}

.path.check {
  stroke-dashoffset: -100;
  -webkit-animation: dash-check .9s .35s ease-in-out forwards;
          animation: dash-check .9s .35s ease-in-out forwards;
}

p {
  text-align: center;
}

p.success {
  color: #73AF55;
}

@-webkit-keyframes dash-check {
  0% {
    stroke-dashoffset: -100;
  }

  100% {
    stroke-dashoffset: 900;
  }
}

@keyframes dash-check {
  0% {
    stroke-dashoffset: -100;
  }

  100% {
    stroke-dashoffset: 900;
  }
}

@font-face {
  font-family: 'Montserrat';
  font-display: optional;
  unicode-range: U+000-5FF;
}

html {
  font-size: 62.5%;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-size: 1.8rem;
  font-size: var(--font-size-regular);
  font-family: 'Montserrat', sans-serif;
  font-family: var(--main-font);
  min-width: 320px;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

main {
  -ms-flex: 1;
      flex: 1;
}

.content {
  text-align: center;
}

.intro {
  margin-top: 7rem;
  margin-bottom: 7rem;
}

@media (max-width: 500px) {
  .intro {
    margin-top: 4rem;
    margin-bottom: 4rem;
  }
}